.all-projects {
    flex: 5;
    padding: 15px;
    display: flex;
    flex-wrap: wrap; 
    justify-content: space-evenly;
    align-items: baseline;
    margin: 20px;
}

.label {
    border-style:groove;
    margin: 25px;
    border-radius: 6px;
    font-size: 25px;
    margin-left: .5%;
    
}

.label img {
    width: 500px;
    height: 265px;
    padding: 0%;
}

.label img:hover {
    box-shadow: 3px 3px 2px rgb(35, 35, 75);
}

/* Project Headers */

.project-header {
    color: rgb(23, 33, 90);
    font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.stoxster-header {
    color: black;
