/* Project 1 button */
.label .btn1 {
    position: relative;
    top: 34%;
    left: 36%;
    font-size: 17px;
    padding: 10px 25px;
    border: none;
    cursor: pointer;
    border-radius: 8px;
    background-color: rgb(61, 61, 61);
  }
  
  .label .btn1 a {
      text-decoration: none;
      color: aliceblue;
  }
  
  .label .btn1:hover {
      background-color: rgb(38, 51, 121);
  }

